<script>
import {
  mdbCard,
  mdbBreadcrumb,
  mdbBreadcrumbItem,
  mdbInput,
  mdbCardBody,
  mdbBtn,
} from "mdbvue";
import ItemCard from "../components/ItemCard";

export default {
  components: {
    mdbCard,
    mdbCardBody,
    ItemCard,
    mdbBreadcrumb,
    mdbBreadcrumbItem,
    mdbInput,
    mdbBtn,
  },
  data() {
    return {
      basicOptions: [
        { text: "Mallas", value: "Mallas" },
        { text: "Accesorios para cercas", value: "Accesorios para cercas" },
        { text: "Concertina", value: "Concertina" },
        { text: "Alambres", value: "Alambres" },
        { text: "Postes", value: "Postes" },
        { text: "Metal desplegado", value: "Metal desplegado" },
        { text: "Tubo galvanizado", value: "Tubo galvanizado" },
        { text: "Láminas", value: "Láminas" },
        {
          text: "Materiales para invernaderos",
          value: "Materiales para invernaderos",
        },
      ],
      categoria: "",
      products: [...this.items],
      mallas: false,
      reja: false,
      concertina: false,
      tubo: false,
      metal: false,
      alambres: false,
      laminas: false,
      materiales: false,
      tuberia: false,
      accesorios: false,
    };
  },
  methods: {
    changeCategory(nueva) {
      this.categoria = nueva;
    },
    reload() {
      this.categoria = "";
    },
    filterCategory(category) {
      this.categoria = category;
      window.scrollTo(0,210);
    },
    filter(data, category) {
      if (data) {
        this.categoria = category;
      } else {
        this.reload();
      }
    },
  },
  props: {
    items: Array,
  },
  watch: {
    categoria(nueva) {
      if (nueva != "") {
        let temp = [...this.items];
        let changes = temp.filter((item) => item.category == nueva);
        this.products = changes;
      } else {
        this.products = [...this.items];
      }
    },
    mallas(n) {
      console.log(n);
    },
  },
  mounted() {
    if (this.$route.params.category) {
      this.categoria = this.$route.params.category;
      let temp = [...this.items];
      let changes = temp.filter(
        (item) => item.category == this.$route.params.category
      );
      this.products = changes;
    }
  },
};
</script>

